	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The SixApart solution: Divest TypePad","date":"16 May 2004","slug":"/2004-05-16-the-sixapart-solution-divest-typepad/","tags":"Y2004"},"html":"<p><a title=\"Software or hosting, they cannot serve two masters\" href=\"http://www.buzzmachine.com/archives/2004_05_15.html#007062\">BuzzMachine: The SixApart solution: Divest TypePad</a></p>"},"next":{"fields":{"title":"MT to WordPress","date":"16 May 2004","slug":"/2004-05-16-mt-to-wordpress/"}},"previous":{"fields":{"title":"0xDECAFBAD: Use Atom for a Universal Blog Transfer Protocol","date":"18 May 2004","slug":"/2004-05-18-0xdecafbad-use-atom-for-a-universal-blog-transfer-protocol/"}}},{"node":{"fields":{"title":"MT to WordPress","date":"16 May 2004","slug":"/2004-05-16-mt-to-wordpress/","tags":"Y2004"},"html":"<p>Some notes on importing MT entries into WordPress: </p>\n<ol>\n<li>The script in the WP RC uses the MT export file, so post numbers are not imported. That might be important if you use the post number in your permalink, like I do.</li>\n<li>MT 3.0 seems to have a bug in the export routine... the dates are missing an AM/PM designation, so all posts and comments posted between noon and midnight are off by 12 hours. MT 2.661 exports from an MT 3.0 database just fine, though.</li>\n<li>The default encoding of WP is UTF-8, so you either have to convert the MT export file from Latin 1 to UTF-8 (either recode or BBEdit works fine), or change the encoding in WP (under Options->Reading) to ISO 8859-1.</li>\n</ol>"},"next":{"fields":{"title":"What WordPress Does Wrong","date":"15 May 2004","slug":"/2004-05-15-what-wordpress-does-wrong/"}},"previous":{"fields":{"title":"The SixApart solution: Divest TypePad","date":"16 May 2004","slug":"/2004-05-16-the-sixapart-solution-divest-typepad/"}}},{"node":{"fields":{"title":"What WordPress Does Wrong","date":"15 May 2004","slug":"/2004-05-15-what-wordpress-does-wrong/","tags":"Y2004"},"html":"<p><img alt=\"wperror.png\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/05/wperror.png\" width=\"320\" height=\"222\" border=\"0\" /></p>\n<p>If your database craps out, your entire site is unreadable.</p>"},"next":{"fields":{"title":"Test Site","date":"15 May 2004","slug":"/2004-05-15-test-site/"}},"previous":{"fields":{"title":"MT to WordPress","date":"16 May 2004","slug":"/2004-05-16-mt-to-wordpress/"}}}],"pathPrefix":"","first":false,"last":false,"index":485,"pageCount":1101}}

